import { Store } from "pullstate";
var m;
var n;
m = 5;
n = 10;
var createcell = function () {
    var output = [];
    for (var i = 0; i < m; i++) {
        output[i] = [];
        for (var j = 0; j < n; j++) {
            output[i].push('');
        }
    }
    return output;
};
export var CellStore = new Store({
    log: createcell(),
    host: "",
    low: m,
    colum: n
});
export var selectanti = function (anti) {
    CellStore.update(function (s) {
        s.host = anti;
    });
};
export var selectcell = function (x, y, host) {
    CellStore.update(function (s) {
        s.log[x][y] = host;
    });
};
