import React from "react";
import './CSSstyle/start.css';
import play_btn from './CSSstyle/image/play.png';
import howtoplay_btn from './CSSstyle/image/howto.png';
import { useNavigate } from "react-router-dom";
var StartMenu = function () {
    var navigate = useNavigate();
    var newGame = function () {
        fetch("/newgame");
        navigate("/uploadfile/main");
    };
    return (React.createElement("div", { className: "bg-body" },
        React.createElement("div", { className: "bg-main" },
            React.createElement("div", { className: "btn-float-right-bottom flex flex-col space-y-4 " },
                React.createElement("button", { className: "btnscale btn-scale" },
                    React.createElement("img", { src: play_btn, alt: "start_button", style: { width: "280px", height: "120px" }, onClick: function () { newGame(); } })),
                React.createElement("button", { className: "btnscale btn-scale" },
                    React.createElement("img", { src: howtoplay_btn, alt: "howto_button", style: { width: "280px", height: "120px" }, onClick: function () { return navigate("/howtoplay"); } }))))));
};
export default StartMenu;
